import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Bargraph.scss';

class Bargraph extends Component {
    componentDidMount(){
        const chart = document.getElementsByClassName("bargraph")[0];
        const bars = chart.querySelectorAll(".bar");
        if (this.props.title == "Birthdays by Month"){
            var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul",'Aug',"Sep","Oct","Nov","Dec"];
            for (var i = 0; i < this.props.rowCount; i++) {
                bars[i].dataset.title =  months[i];
            }
        } else if (this.props.title == "Lifespans") {
            var spans = ["20-30","31-40","41-50","51-60","61-70","71-80","81-90","91-100","100+"];
            for (var i = 0; i < this.props.rowCount; i++) {
                bars[i].dataset.title =  spans[i];
            }
        } else if (this.props.title == "Common Birthdays"){
            function randomDate(start, end) {
                var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul",'Aug',"Sep","Oct","Nov","Dec"];
                var month = Math.floor(Math.random() * 12);
                var day = Math.floor(Math.random() * 30) + 1;
            
                return months[month] + "  " + day;
            }
            for (var i = 0; i < this.props.rowCount; i++) {
                var date = randomDate(new Date(2019, 0, 1), new Date(2020, 0, 1));
                bars[i].dataset.title =  date;
            }
        } else  {
            for (var i = 0; i < this.props.rowCount; i++) {
                bars[i].dataset.title =  i+1;
            }
        }
    
        function populateChart() {
            for (let bar of bars) {
                let val = bar.dataset.val;
                setValOfBar(val, bar);
            }
        }
        
        function setValOfBar(val, bar) {
            bar.style.height = val * 20 + "px";
            bar.classList.toggle("flip-percentage", val < 50);
        }
        
        populateChart();
        
      
    }
  render() {
    var rows = [];
    var props = {};
    if (this.props.title == "Lifespans"){
        props.page = " - Lifespans";
        props.headerType = "normal";
        props.title1 = "People who died between ";
    } else if (this.props.title == "Common Birthdays"){
        props.page = " - Birthdays";
        props.headerType = "birthdays";
        props.title1 = "People who have a birthday on ";
        props.tab = "main";
    } else {
        props.page = " - Children";
        props.headerType = "normal";
        props.title1 = "Women who had ";
        props.title2 = " children"
    }
    for (var i = 0; i < this.props.rowCount; i++) {
        let val = Math.floor(Math.random() * 12);

        rows.push(<Link data-val={val} to={{pathname: "/"+this.props.linkTo, state:{props: props, i: i, value: val}}}  className="bar"></Link>);
    }
    return (
        <div className="bargraphWrapper">
            <div className="bargraphTitle">{this.props.title}</div>
            <div className="graphDescription">{this.props.description}</div>
            <div className="yAxis">{this.props.y}</div>
            <div className="xAxis">{this.props.x}</div>
            <div className="bargraph">
                {rows}
            </div>
            <div className="barInstructions">Click a bar</div>
        </div>
    );
  }
  
}

export default Bargraph;
