import React, { Component } from 'react';
import './subHeader.css';
import Tab from "./tab"

class Subheader extends Component {
  render() {
    if (this.props.familyGroup == "all"){
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" selected="true" linkTo={this.props.linkTo}/>
            <Tab name="Mother's Ancestors" linkTo={this.props.linkTo}/>
            <Tab name="Father's Ancestors"  linkTo={this.props.linkTo}/>
        </div>
    );
    } else if (this.props.familyGroup == "mother"){
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" linkTo={this.props.linkTo} />
            <Tab name="Mother's Ancestors" selected="true" linkTo={this.props.linkTo} />
            <Tab name="Father's Ancestors" linkTo={this.props.linkTo} />
        </div>
    );
    } else {
      return (
        <div className='subHeader'>
            <Tab name="Your Ancestors" linkTo={this.props.linkTo} />
            <Tab name="Mother's Ancestors" linkTo={this.props.linkTo} />
            <Tab name="Father's Ancestors" selected="true" linkTo={this.props.linkTo} />
        </div>
    );
    }
  }
}

export default Subheader;
