import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import './tab.scss';

class Tab extends Component {
  render() {
    if (this.props.selected == "true"){
      return (
        <Link to={this.props.linkTo} className="tab selectedTab">
        {this.props.name}
        </Link>
    );
    } else {
      return (
        <Link to={this.props.linkTo} className="tab">
        {this.props.name}
        </Link>
    );
    }
   
  }
}

export default Tab;
