import React, { Component } from 'react';
import './birthdaysSubHeader.scss';
import {Link} from 'react-router-dom'
import Tab from "./tab"

class BirthdaysSubHeader extends Component {
  render() {
      if (this.props.selectedTab == "main"){
        return (
            <div className='birthdaysHeader'>
                <Tab name="Common Birthdays" selected="true" linkTo="birthdays" />
                <Tab name="Birthdays by Month" linkTo="birthdayMonths" />
            </div>
        );
      } else {
        return (
            <div className='birthdaysHeader'>
                <Tab name="Common Birthdays" linkTo="birthdays" />
                <Tab name="Birthdays by Month" selected="true" linkTo="birthdayMonths" />
            </div>
        );
      }
      
  }
}

export default BirthdaysSubHeader;
