import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route } from 'react-router-dom';
import * as serviceWorker from './serviceWorker';

import App from "./components/App";
import HomePage from "./components/home/HomePage";
import AboutPage from "./components/about/AboutPage";
import Lifespans from "./components/lifespans/Lifespans"
import Children from "./components/children/Children";
import Birthdays from "./components/birthdays/Birthdays";
import BirthdayMonths from "./components/birthdays/BirthdayMonths";
import Header from "./components/header/Header";

import './index.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import LifespansGraph from './components/lifespans/LifespansGraph';
import PersonRows from './components/shared/personRows';
import Calendar from './components/birthdays/Calendar';

ReactDOM.render(
	<BrowserRouter basename={process.env.PUBLIC_URL}>
		<div>
			<Route path='/' component={App} />
			<Route exact path='/' component={HomePage} />
			<Route exact path='/about' component={AboutPage} />
			<Route exact path="/lifespans" component={Lifespans} />
			<Route exact path="/birthdays" component={Birthdays}/>
			<Route exact path="/children" component={Children}/>
			<Route exact path="/lifespansGraph" component={LifespansGraph}/>
			<Route exact path="/personRows" component={PersonRows}/>
			<Route exact path="/birthdayMonths" component={BirthdayMonths}/>
			<Route exact path="/calendar" component={Calendar}/>
		</div>
	</BrowserRouter>,
	document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
