import React, { Component } from 'react';
import Header from "../header/Header"
import Subheader from "../subHeader/subHeader"
import BirthdayHeader from "../subHeader/birthdaysSubHeader"
import './personRows.scss';

class PersonRows extends Component {
  render() {
    var defaultNames = ["Esti Dupond",
    "Abraham Krastev",
    "Maighread Rovigatti",
    "Markuss Chandler",
    "Wealhmær Abel",
    "Tertius Harrison",
    "Aksel Abraham",
    "Pedja Sigurðsson",
    "Aldebrand Berglund",
    "Gwilym Muhammad",
    "Kinga Barzetti",
    "Pari Andela",
    "Yahui Agnarsson",
    "Zeno Chandler",
    "Karthika Caron",
    "Helena Bourreau",
    "Maxwell Mac Leòid",
    "Bradán Schnur",
    "Lodovico Nye",
    "Amílcar Croce",
    "Flann Blackburn",
    "Heitor Alberghini",
    "Lydos Irving",
    "Lila Descoteaux",
    "Bojana Dane",
    "Gioconda Lenox",
    "Luitgard Babić",
    "Nicole Nacar",
    "Dorean Olesen",
    "Hrólfr Acardi",
    "Dema Abraham",
    "Breno Aquila",
    "Felicitas Valentinov",
    "Willka Larue",
    "Annette Kayode"
    
        ];
    if (this.props.location.state.props.headerType === "normal"){
        if (this.props.location.state.props.page == " - Children"){
            var names = [];
            for (var i = 0; i < this.props.location.state.value; i++) {
                this.titleBolded = this.props.location.state.i;
                names.push(<a href="http://familysearch.org" target="_blank" className="row">{defaultNames[Math.floor(Math.random() * defaultNames.length)]}</a>);
            }
            this.linkTo = "children";
        } else { // Lifespans
            var spans = ["20-30","31-40","41-50","51-60","61-70","71-80","81-90","91-100","100+"];
            this.titleBolded = spans[this.props.location.state.i];
            var names = [];
            for (var i = 0; i < this.props.location.state.value; i++) {
                names.push(<a href="http://familysearch.org" target="_blank" className="row">{defaultNames[Math.floor(Math.random() * defaultNames.length)]}</a>);
            }
            this.linkTo = "lifespans";
        }

        return (
            <div className="personRows">
            <Header name={this.props.location.state.props.page} home="true"/>		
            <Subheader familyGroup="all" linkTo={this.linkTo}/>
                <div className="personRows-Wrapper">
                    <div className="personRows-Title">
                        {this.props.location.state.props.title1} <span className="boldedText">{this.titleBolded}</span>{this.props.location.state.props.title2}:
                    </div>
                    <div className="personRows-Row">
                        {names}
                    </div>
                </div>
            </div>
        );
    } else {
        var names = [];
        for (var i = 0; i < this.props.location.state.value; i++) {
            names.push(<a href="http://familysearch.org" target="_blank" className="row">{defaultNames[Math.floor(Math.random() * defaultNames.length)]}</a>);
        }
        if (this.props.location.state.props.page == " - Date Details"){
            this.titleBolded = this.props.location.state.props.month + " " +  this.props.location.state.date;
        }
        return (
            <div className="personRows">
            <Header name={this.props.location.state.props.page} home="true"/>		
            <BirthdayHeader selectedTab={this.props.location.state.props.tab}/>
                <div className="personRows-Wrapper">
                    <div className="personRows-Title">
                        {this.props.location.state.props.title1} <span className="boldedText">{this.titleBolded}</span>:
                    </div>
                    <div className="personRows-Row">
                        {names}
                    </div>
                </div>
            </div>
        );
    }
   
  }
}

export default PersonRows;
