import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from "../header/Header"
import Subheader from "../subHeader/subHeader"
import Bargraph from "../bargraph/Bargraph"
import BirthdayHeader from "../subHeader/birthdaysSubHeader"
import "./Birthdays.scss"


class CalendarView extends Component {

	render() {
        var row1 = ["1","2","3","4","5","6","7"];
        var row2 = ["8","9","10","11","12","13","14"];
        var row3 = ["15","16","17","18","19","20","21"];
        var row4 = ["22","23","24","25","26","27","28"];
        var row5 = ["29","30","31"];
        var row1Divs = [];
        var row2Divs = [];
        var row3Divs = [];
        var row4Divs = [];
        var row5Divs = [];
        var props = {month: this.props.month, page: " - Date Details", title1: "People who have a birthday on ", tab: "other"};
        for (var i = 0; i < row1.length; i++){
            var number1 = Math.floor(Math.random() * 12);
            var number2 = Math.floor(Math.random() * 12);
            var number3 = Math.floor(Math.random() * 12);
            var number4 = Math.floor(Math.random() * 12);
            var number5 = Math.floor(Math.random() * 12);
            row1Divs.push(<Link to={{pathname: "/personRows", state:{props: props, date: row1[i], i: i, value: number1}}} className="dateBox"><span className="date">{row1[i]}.</span><span className="number">{number1}</span></Link>);
            row2Divs.push(<Link to={{pathname: "/personRows", state:{props: props, date: row2[i], i: i, value: number2}}} className="dateBox"><span className="date">{row2[i]}.</span><span className="number">{number2}</span></Link>);
            row3Divs.push(<Link to={{pathname: "/personRows", state:{props: props, date: row3[i], i: i, value: number3}}} className="dateBox"><span className="date">{row3[i]}.</span><span className="number">{number3}</span></Link>);
            row4Divs.push(<Link to={{pathname: "/personRows", state:{props: props, date: row4[i], i: i, value: number4}}} className="dateBox"><span className="date">{row4[i]}.</span><span className="number">{number4}</span></Link>);
            if (i < 3){
                row5Divs.push(<Link to={{pathname: "/personRows", state:{props: props, date: row5[i], i: i, value: number5}}} className="dateBox"><span className="date">{row5[i]}.</span><span className="number">{number5}</span></Link>);
            }
        }
        
		return (
			<div className="calendarWrapper">
            <div className="monthTitle">{this.props.month}</div>
                <div className="calendarRow">
                    {row1Divs}
                </div>
                <div className="calendarRow">
                    {row2Divs}
                </div>
                <div className="calendarRow">
                    {row3Divs}
                </div>
                <div className="calendarRow">
                    {row4Divs}
                </div>
                <div className="calendarRow">
                    {row5Divs}
                </div>
			</div>
		);
	}
}

export default CalendarView;